.mainLink {
  color: #fff;
  padding: 10px;
}
.mainLink:hover {
  color: rgb(255, 102, 0);
  text-decoration: none;
}
.link {
  color: #fff;
  padding-left: 20px;
}
.link:hover {
  color: rgb(255, 102, 0);
  text-decoration: none;
}
.CarouselImg {
  position: relative;
  max-height: 350px;
  -o-object-fit: cover;
  object-fit: cover;
  background-position: center;
  background-size: cover;
}
.word {
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainContentbg {
  /* background-color: #292929; */
  background-image: url('https://images.unsplash.com/photo-1530818718778-8b666f576020?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1383&q=80');
}
.hrLine {
  background-color: #fff;
  width: 50%;
  margin: 0;
  height: 2px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}
.newProductImg {
  max-width: 650px;
}
.googleMap {
  width: 100%;
  height: 450px;
  border: 0;
  /* allowfullscreen: '';
  loading: 'lazy'; */
}
@media (max-width: 768px) {
  .footerIcon {
    display: none;
  }
}
.warrantlyBorder {
  border: 1px solid white;
}
.checkboxStyle {
  position: relative;
  top: 5px;
  color: rgb(255, 102, 0);
  background-color: rgb(255, 102, 0);
}
.shopBtn {
  /* display: none; */
  visibility: hidden;
}
.cardItem:hover .shopBtn {
  /* display: block; */
  visibility: visible;
}
@media (max-width: 768px) {
  .shopBtn {
    /* display: block; */
    visibility: visible;
  }
}
.shopCartIcon {
  padding-right: 5px;
  color: #fff;
}
.shopLink {
  display: none;
}
@media (max-width: 768px) {
  .shopCartIcon {
    display: none;
  }
  .shopLink {
    display: block;
  }
}
.badge {
  position: absolute;
  top: 8px;
  right: 10px;
}
.infoLink {
  color: black;
}
.infoLink:hover {
  text-decoration: none;
  color: gray;
}
.submitButton {
  padding-top: 60px;
}
.modalLink {
  color: white;
}
.modalLink:hover {
  color: #fff;
  text-decoration: none;
}
@media (max-width: 768px) {
  .productText {
    display: none;
  }
}
.bighead {
  border-radius: 50%;
  min-width: 150px;
}
.carouselIndex {
  z-index: auto;
}
.cartPadding {
  padding-top: 160px;
  padding-bottom: 130px;
}
@media (max-width: 768px) {
  .cartPadding {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.hoverText:hover {
  color:rgb(255, 102, 0)
}